<template>
  <div id="app" class="small-container">
    <h1>Dod</h1>
    <dod-form @ask:question="askQuestion" />
    <dod-table :dods="dods" />
  </div>
</template>

<script>
import DodForm from '@/components/DodForm.vue'
import DodTable from '@/components/DodTable.vue'
import axios from 'axios'

export default {
  name: 'app',
  components: {
    DodForm,
    DodTable
  },
  data() {
    return {
      dods: [
      ],
      current_answer: '',
    }
  },
  methods: {
  async askQuestion(dod) {
      let dodurl = 'https://dod-api-server.azurewebsites.net/dod/' + dod.question;
      // await axios.get('https://api.coindesk.com/v1/bpi/currentprice.json').then(response => (this.current_answer = response))
      await axios.get(dodurl).then(response => (this.current_answer = response.data.data))
      const lastId =
      this.dods.length > 0
      ? this.dods[this.dods.length - 1].id
      : 0;
      const id = lastId + 1;
      const newDod = { ...dod, id };
      newDod.answer = this.current_answer;
      this.dods = [...this.dods, newDod]
    }
  }
}
</script>